import React, { useEffect, useState } from 'react';
import axios from 'axios';
import SignUp from './pages/SignUp';

function App() {
    const [hello, setHello] = useState('');

    useEffect(() => {
        axios
            .get('/api/hello')
            .then((response) => setHello(response.data))
            .catch((error) => console.log(error));
    }, []);

    return (
        <div>
               : {hello}
            <SignUp />
        </div>
    );
}

export default App;
